import React from "react";
import "./style.css";
import { Link } from "react-router-dom";

export const Tooltip = () => (
  <div className="App-tooltip">
    <Link to="/editing" className="App-tooltip-button" />
    <div className="tooltip-header">  </div>
    <div className="tooltip-details">14 , 15:0017:00   </div>
    <div className="tooltip-participants">
      <div className="tooltip-participants-main">
        <div className="icon vader" />
         
      </div>
      <div className="tooltip-participants-rest">&nbsp;  12 </div>
    </div>
  </div>
);



