import React from "react";
import "./style.css";

export const TitleInput = ({ value, onChange }) => (
  <div className="title-input">
    <div className="editing-input-title">  </div>
    <input
      className="editing-input"
      placeholder={"   ?"}
      value={value}
      onChange={onChange}
    />
  </div>
);



